import axios from 'axios'

const server = process.env.REACT_APP_BACKEND_URL;
export const GET_KELOMPOK = "GET_KELOMPOK"

export const getListKelompok = () => {
    return (dispatch) => {
        axios
            .get(server + "groups")
            .then((res) =>
                dispatch({
                    type: GET_KELOMPOK,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch((err) =>
                dispatch({
                    type: GET_KELOMPOK,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}
