
import { SET_DASHBOARD, SET_DEFAULT, SET_FICO, SET_LOGIN, SET_MASTER, SET_STOKING, SET_TITLE, SET_TRANSAKSI} from '../actions/titleAction'

const initialState= {
    title: "AYU GOLD",
    Dashboard: "sidebar-item",
    Master: "sidebar-item",
    Stoking: "sidebar-item",
    Transaksi: "sidebar-item",
    Fico: "sidebar-item",
    Login : true,
}

const title = (state = initialState, action) => {
   switch(action.type){
       case SET_TITLE:
           return{
               ...state,
               title: action.payload.title
           }
        case SET_DASHBOARD:
            return{
                ...state,
                Dashboard: action.payload.Dashboard,
                Master: "sidebar-item",
                Stoking: "sidebar-item",
                Transaksi: "sidebar-item",
                Fico: "sidebar-item",
            }
        case SET_MASTER:
            return{
                ...state,
                Master: action.payload.Master,
                Dashboard: "sidebar-item",
                Stoking: "sidebar-item",
                Transaksi: "sidebar-item",
                Fico: "sidebar-item",
            }
        case SET_STOKING:
            return{
                ...state,
                Stoking: action.payload.Stoking,
                Dashboard: "sidebar-item",
                Master: "sidebar-item",
                Transaksi: "sidebar-item",
                Fico: "sidebar-item",
            }
        case SET_TRANSAKSI:
            return{
                ...state,
                Transaksi: action.payload.Transaksi,
                Dashboard: "sidebar-item",
                Master: "sidebar-item",
                Stoking: "sidebar-item",
                Fico: "sidebar-item",
            }
        case SET_FICO:
            return{
                ...state,
                Fico: action.payload.Fico,
                Dashboard: "sidebar-item",
                Master: "sidebar-item",
                Stoking: "sidebar-item",
                Transaksi: "sidebar-item",
            }
        case SET_DEFAULT:
            return{
                ...state,
                title: "AYU GOLD",
                Dashboard: "sidebar-item",
                Master: "sidebar-item",
                Stoking: "sidebar-item",
                Transaksi: "sidebar-item",
                Fico: "sidebar-item",
            }
        case SET_LOGIN: 
        return{
            ...state,
            Login : true
        }
        default: 
        return state 
   }
}
 
export default title;