import axios from 'axios'


export const GET_KOTA_LIST = "GET_KOTA_LIST"
export const EDIT_KOTA = "EDIT_KOTA"

const server = process.env.REACT_APP_BACKEND_URL;
export const getKotaList = () => {
    return (dispatch) => {
        axios
        .get(server + "citys")
        .then((res) =>
          dispatch({
              type : GET_KOTA_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_KOTA_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const editKota = (kodeKota, namaKota, kodeProvinsi, status) => {
    return (dispatch) => {
        dispatch({
            type : EDIT_KOTA,
            payload: {
                kodeKota: kodeKota,
                namaKota: namaKota,
                kodeProvinsi: kodeProvinsi,
                status: status
            }
        })
    }
}
