export const setDefault = (isi) => {
    return(dispatch) => {
        dispatch({
            type: SET_DEFAULT,
            payload: {
                Transaksi: "sidebar-item",
                Fico: "sidebar-item",
                Stoking: "sidebar-item",
                Dashboard: "sidebar-item",
                Master: "sidebar-item",
                title: isi
            }
        })
    }
}

export const setLogin = ()=> {
    return(dispatch) => {
        dispatch({
            type: SET_LOGIN,
            payload: {
                data : "TRUE"
            }
        })
    }
}