import axios from 'axios'


export const GET_PROVINSI_LIST = "GET_PROVINSI_LIST";
export const EDIT_PROVINSI = "EDIT_PROVINSI"
export const DELETE_PROVINSI = "DELETE_PROVINSI"

const server = process.env.REACT_APP_BACKEND_URL;
export const getProvinsiLIst = () => {
    return (dispatch) => {
        axios
        .get(server + "provinces")
        .then((res) =>
          dispatch({
              type : GET_PROVINSI_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_PROVINSI_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const editProvinsiList = (namaProvinsi, kodeProvinsi) => {
    return (dispatch) => {
        dispatch({
            type : EDIT_PROVINSI,
            payload: {
                namaProvinsi: namaProvinsi,
                kodeProvinsi: kodeProvinsi 
            }
        })
    }
}
export const deleteProvinsiList = (kode, link) => {
    return (dispatch) => {
        axios
        .delete(server + link, {
            data: {
                kodeProvinsi: kode
            }
        } )
        .then((res) =>
          console.log(res)
        )
        .catch((err) =>
            console.log(err)
        )
    }
}


