import axios from 'axios'


export const GET_SUPPLIER_LIST = "GET_SUPPLIER_LIST"
export const EDIT_SUPPLIER = "EDIT_SUPPLIER"

const server = process.env.REACT_APP_BACKEND_URL;
export const getsupplierList = () => {
    return (dispatch) => {
        axios
            .get(server + "suppliers")
            .then((res) =>
                dispatch({
                    type: GET_SUPPLIER_LIST,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch((err) =>
                dispatch({
                    type: GET_SUPPLIER_LIST,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}

export const editsupplier = (
    Alamat,
    Email,
    atasNama,
    contactPerson,
    fax,
    handPhone,
    kodeExt,
    kodeKota,
    kodePos,
    kodeSupplier,
    namaBank,
    namaSupplier,
    nomorRekening,
    telepon, namaKota) => {
    return (dispatch) => {
        dispatch({
            type: EDIT_SUPPLIER,
            payload: {
                Alamat: Alamat,
                Email: Email,
                atasNama: atasNama,
                contactPerson: contactPerson,
                fax: fax,
                handPhone: handPhone,
                kodeExt: kodeExt,
                kodeKota: kodeKota + "|" + namaKota,
                kodePos: kodePos,
                kodeSupplier: kodeSupplier,
                namaBank: namaBank,
                namaSupplier: namaSupplier,
                nomorRekening: nomorRekening,
                telepon: telepon,

            }
        })
    }
}

