import axios from 'axios'


export const GET_PEMBAYARANSUPPLIER_LIST = "GET_PEMBAYARANSUPPLIER_LIST"

const server = process.env.REACT_APP_BACKEND_URL;
export const getpembayaranSupplierList = () => {
    return (dispatch) => {
        axios
        .get(server + "subcategorys")
        .then((res) =>
          dispatch({
              type : GET_PEMBAYARANSUPPLIER_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_PEMBAYARANSUPPLIER_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

