import axios from 'axios'


export const GET_KADAR_LIST = "GET_KADAR_LIST"
export const EDIT_KADAR = "EDIT_KADAR"

const server = process.env.REACT_APP_BACKEND_URL;
export const getKadarList = (input) => {
    return (dispatch) => {
        axios
        .get(server + "rates?tipeKadar=" +input)
        .then((res) =>
          dispatch({
              type : GET_KADAR_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_KADAR_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const editKadar = (kadar, tipe, status) => {
    return (dispatch) => {
        dispatch({
            type : EDIT_KADAR,
            payload: {
                kadar: kadar,
                tipeKadar: tipe,
                statusAktif: status
            }
        })
    }
}

