import axios from 'axios'


export const GET_CUSTOMER_LIST = "GET_CUSTOMER_LIST"
export const EDIT_CUSTOMER = "EDIT_CUSTOMER"

const server = process.env.REACT_APP_BACKEND_URL;
export const getcustomerList = () => {
    return (dispatch) => {
        axios
            .get(server + "customers")
            .then((res) =>
                dispatch({
                    type: GET_CUSTOMER_LIST,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch((err) =>
                dispatch({
                    type: GET_CUSTOMER_LIST,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}

export const editcustomer = (
    alamat,
    email,
    handPhone,
    kodeArea,
    kodeCustomer,
    kodeExt,
    kodeKota,
    kodePos,
    namaCustomer,
    namaToko,
    status,
    tanggalDaftar,
    telepon,
    namaKota,
    namaArea,
) => {
    return (dispatch) => {
        dispatch({
            type: EDIT_CUSTOMER,
            payload: {
                alamat: alamat,
                email: email,
                handPhone: handPhone,
                kodeArea: kodeArea + "|" + namaArea,
                kodeCustomer: kodeCustomer,
                kodeExt: kodeExt,
                kodeKota: kodeKota + "|" + namaKota,
                kodePos: kodePos,
                namaCustomer: namaCustomer,
                namaToko: namaToko,
                status: status,
                tanggalDaftar: tanggalDaftar,
                telepon: telepon,
            }
        })
    }
}

