import React from "react";
import Logo from "./Images/logo_test.svg";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
const mapStateToProps = (state) => {
  return {
    title: state.title.title,
  };
};

const Header = (props) => {
  return (
    <header className={props.topbar} data-navbarbg="skin6">
      <nav className="navbar top-navbar navbar-expand-md">
        <div className="navbar-header" data-logobg="skin6">
          <div className="navbar-brand">
            <Link to="/dashboard">
              <b className="logo-icon">
                <img
                  src={Logo}
                  alt="homepage"
                  className="dark-logo"
                  style={{ width: 40, height: 40 }}
                />
                <img src={Logo} alt="homepage" className="light-logo" />
              </b>
            </Link>
          </div>
          <a
            className="topbartoggler d-block d-md-none waves-effect waves-light"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
            href="/dashboard"
          >
            <i className="ti-more" />
          </a>
        </div>
        <div className="navbar-collapse collapse" id="navbarSupportedContent">
          <ul className="navbar-nav float-left mr-auto mt-auto ml-3 pl-1">
            <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
              {props.title}
            </h4>
          </ul>
          <ul className="navbar-nav float-right">
            <li className="nav-item dropdown">
              <a
                href="/dashboard"
                className="nav-link dropdown-toggle"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <img
                  src={Logo}
                  alt="user"
                  className="rounded-circle"
                  width={40}
                />
                <span className="ml-2 d-none d-lg-inline-block">
                  <span>Hello,</span>{" "}
                  <span className="text-dark">Jason Doe</span>{" "}
                  <i data-feather="chevron-down" className="svg-icon" />
                </span>
              </a>
              <div className="dropdown-menu dropdown-menu-right user-dd animated flipInY">
                <Link className="dropdown-item" to="/kosong">
                  <i data-feather="user" className="svg-icon mr-2 ml-1" />
                  My Profile
                </Link>
                <Link className="dropdown-item" to="/kosong">
                  <i
                    data-feather="credit-card"
                    className="svg-icon mr-2 ml-1"
                  />
                  My Balance
                </Link>
                <Link className="dropdown-item" to="/kosong">
                  <i data-feather="mail" className="svg-icon mr-2 ml-1" />
                  Inbox
                </Link>
                <div className="dropdown-divider" />
                <Link className="dropdown-item" to="/kosong">
                  <i data-feather="settings" className="svg-icon mr-2 ml-1" />
                  Account Setting
                </Link>
                <div className="dropdown-divider" />
                <Link className="dropdown-item" to="/kosong">
                  <i data-feather="power" className="svg-icon mr-2 ml-1" />
                  Logout
                </Link>
                <div className="dropdown-divider" />
              </div>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
};

export default connect(mapStateToProps, null)(Header);
