import React, { Component,lazy, Suspense } from 'react';
import './App.css';
import logo from './Images/logo_test.svg'
import Sidebar from "./Sidebar";
import Header from "./Header";
import { connect } from 'react-redux';
import { setDefault, setLogin } from './actions/titleAction';
import { RouteLink } from './Route';
import Skeleton from 'react-loading-skeleton';

const Route = lazy(() => import("./Route"));



const maptoState = (state) => {
  return {
    login: state.title.Login,
  }
}
class App extends Component {


  componentDidMount() {
    this.props.dispatch(setDefault())
  }
  render() {
    var link = window.location.pathname;
    switch (link) {
      case "/ayugold.com/provinsi":
        link = "Data Provinsi";
        break;
      case "/ayugold.com/dashboard":
        link = "Dashboard"
        break;
      case "/ayugold.com/kota":
        link = "Data Kota"
        break;
      case "/ayugold.com/area":
        link = "Data Area"
        break;
      case "/ayugold.com/rekening":
        link = "Data Rekening"
        break;
      case "/ayugold.com/lokasi":
        link = "Data Lokasi"
        break;
      case "/ayugold.com/salesman":
        link = "Data Salesman"
        break;
      case "/ayugold.com/supplier":
        link = "Data Supplier"
        break;
      case "/ayugold.com/customer":
        link = "Data Customer"
        break;
      case "/ayugold.com/koderosok":
        link = "Data Kode Rosok dan Ciok"
        break;
      case "/ayugold.com/saldoawal":
        link = "Data Saldo Awal"
        break;
      case "/ayugold.com/kadar":
        link = "Data Kadar"
        break;
      case "/ayugold.com/KlasifikasiBarang":
        link = "Klasifikasi Barang"
        break;
      case "/ayugold.com/parameter":
        link = "Parameter Transaksi Keuangan"
        break;
      case "/ayugold.com/pindahbarang":
        link = "Pindah Barang"
        break;
      case "/ayugold.com/mutasibarang":
        link = "Mutasi Barang"
        break;
      case "/ayugold.com/mutasiciok":
        link = "Mutasi Ciok"
        break;
      case "/ayugold.com/kirimmasak":
        link = "Kirim Masak"
        break;
      case "/ayugold.com/terimamasak":
        link = "Terima Masak"
        break;
      case "/ayugold.com/kirimbahan":
        link = "Kirim Bahan"
        break;
      case "/ayugold.com/terimasupplier":
        link = "Penerimaan Supplier"
        break;
      case "/ayugold.com/returnsupplier":
        link = "Return Supplier"
        break;
      case "/ayugold.com/tolakanreturnsupplier":
        link = "Tolakan Return Supplier"
        break;
      case "/ayugold.com/ambilbarcode":
        link = "Transaksi Pengambilan Barcode"
        break;
      default:
        link = "AYU GOLD"
    }
    if (this.props.login === true) {
      return (
        <div id="main-wrapper" data-theme="light" data-layout="vertical" data-navbarbg="skin6" data-sidebartype="mini-sidebar"
          data-sidebar-position="fixed" data-header-position="fixed" data-boxed-layout="full">
          <Header topbar="topbar" />
          <Sidebar sidebar="left-sidebar" />
          <div className="page-wrapper">
            <RouteLink/>
          </div>
        </div>
      );
    } else {
      return (
        <div className="main-wrapper">
          <Header topbar="topbar d-none" />
          <Sidebar sidebar="left-sidebar d-none" />
          <div className="auth-wrapper d-flex no-block justify-content-center align-items-center position-relative">
            <div className="auth-box row" style={{ width: "80%" }}>
              <div className="col-lg-7 col-md-5 modal-bg-img" style={{ backgroundImage: 'url(./logo_test.svg)' }}>
                <img src={logo} alt="Logo Perusahaan" />
              </div>
              <div className="col-lg-5 col-md-7 bg-white">
                <div className="p-3">
                  <h2 className="mt-3 text-center">Selamat Datang</h2>
                  <p className="text-center">Silahkan masukan Email dan password untuk Masuk Kedalam sistem.</p>
                  <form className="mt-4">
                    <div className="row">
                      <div className="col-lg-12">
                        <div className="form-group">
                          <label className="text-dark" htmlFor="uname">Email</label>
                          <input className="form-control" id="uname" type="text" placeholder="Masukan Email" />
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div className="form-group">
                          <label className="text-dark" htmlFor="pwd">Password</label>
                          <input className="form-control" type="password" id="pwd" placeholder="Masukan Password" />
                        </div>
                      </div>
                      <div className="col-lg-12 text-center">
                        <button type="submit" className="btn btn-block btn-dark" onClick={() => this.props.dispatch(setLogin())}>Masuk</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default connect(maptoState, null)(App);
