import axios from 'axios'

const server = process.env.REACT_APP_BACKEND_URL;
export const GET_KODE_BARANG_LIST = "GET_KODE_BARANG_LIST"
export const EDIT_KODE_BARANG = "EDIT_KODE_BARANG"

export const getListBarang = () => {
    return (dispatch) => {
        axios
            .get(server + "subcategorys2?kodeKelompok=PERHIASAN")
            .then((res) =>
                dispatch({
                    type: GET_KODE_BARANG_LIST,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch((err) =>
                dispatch({
                    type: GET_KODE_BARANG_LIST,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}

export const editRosok = (kodeJenis, statusAktif, kodeKategori, namaJenis, kadarBeli, kadarJual, kadarCetak) => {
    return (dispatch) => {
        dispatch({
            type: EDIT_KODE_BARANG,
            payload: {
                kodeJenis: kodeJenis,
                statusAktif: statusAktif,
                kodeKategori: kodeKategori,
                namaJenis: namaJenis,
                kadarBeli: kadarBeli,
                kadarJual: kadarJual,
                kadarCetak: kadarCetak
            }
        })
    }
}
