import axios from 'axios'


export const GET_KATEGORI_LIST = "GET_KATEGORI_LIST"
export const EDIT_KATEGORI = "EDIT_KATEGORI"

const server = process.env.REACT_APP_BACKEND_URL;
export const getkategoriList = () => {
    return (dispatch) => {
        axios
            .get(server + "categorys")
            .then((res) =>
                dispatch({
                    type: GET_KATEGORI_LIST,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch((err) =>
                dispatch({
                    type: GET_KATEGORI_LIST,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}
export const editkategoriList = (statusAktif, kodeKategori, keterangan, kodeGroup, kodeIntern, statusValid) => {
    return (dispatch) => {
        dispatch({
            type: EDIT_KATEGORI,
            payload: {
                statusAktif: statusAktif,
                kodeKategori: kodeKategori,
                keterangan: keterangan,
                kodeGroup: kodeGroup,
                kodeIntern: kodeIntern,
                statusValid: statusValid
            }
        })
    }
}

