import axios from 'axios'


export const GET_AREA_LIST = "GET_AREA_LIST"
export const EDIT_AREA = "EDIT_AREA"

const server = process.env.REACT_APP_BACKEND_URL;
export const getareaList = () => {
    return (dispatch) => {
        axios
        .get(server + "areas")
        .then((res) =>
          dispatch({
              type : GET_AREA_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_AREA_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const editarea = (kodeArea, namaArea, kodeKota, Status) => {
    return (dispatch) => {
        dispatch({
            type : EDIT_AREA,
            payload: {
                kodeArea: kodeArea,
                namaArea: namaArea,
                kodeKota: kodeKota,
                Status: Status
            }
        })
    }
}

