import axios from 'axios'


export const GET_REKENING_LIST = "GET_REKENING_LIST"
export const EDIT_REKENING_LIST = "EDIT_REKENING_LIST"

const server = process.env.REACT_APP_BACKEND_URL;
export const getrekeningList = () => {
    return (dispatch) => {
        axios
        .get(server + "banks")
        .then((res) =>
          dispatch({
              type : GET_REKENING_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_REKENING_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const editRekening = (noRekening, atasNama, namaBank, mataUang) => {
    return (dispatch) => {
        dispatch({
            type : EDIT_REKENING_LIST,
            payload: {
                noRekening: noRekening,
                atasNama: atasNama,
                namaBank: namaBank,
                mataUang: mataUang,
            }
        })
    }
}

