
import { EDIT_PROVINSI, GET_PROVINSI_LIST } from '../actions/provinsiAction'
import { EDIT_KOTA, GET_KOTA_LIST } from '../actions/kotaAction'
import { GET_AREA_LIST, EDIT_AREA } from '../actions/areaAction'
import { EDIT_REKENING_LIST, GET_REKENING_LIST } from '../actions/rekeningAction'
import { GET_LOKASI_LIST, EDIT_LOKASI } from '../actions/lokasiAction'
import { EDIT_SALESMAN, GET_SALESMAN_LIST } from '../actions/salesmanAction'
import { EDIT_SUPPLIER, GET_SUPPLIER_LIST } from '../actions/supplierAction'
import { EDIT_CUSTOMER, GET_CUSTOMER_LIST } from '../actions/customerAction'
import { EDIT_KATEGORI, GET_KATEGORI_LIST } from '../actions/kategoriAction'
import { GET_PEMBAYARANSUPPLIER_LIST } from '../actions/pembayaranSupplierAction'
import { GET_SIDEBAR_UPDATE } from '../actions/sidebarAction'
import { EDIT_KADAR, GET_KADAR_LIST } from '../actions/kadarAction'
import { EDIT_ROSOK, GET_ROSOK_LIST } from '../actions/rosokAction'
import { GET_PARAMETER_TRANSAKSI } from '../actions/parameterTransaksiAction'
import { GET_KELOMPOK } from '../actions/kelompokAction'
import { GET_KODE_BARANG_LIST } from '../actions/kodeBarangAction'
import { GET_UUID } from '../actions/fakturAction'
import { TOTAL_BON } from '../actions/TrasferCustomerAction'

const initialState = {
  provinsi: false,
  editProvinsi: false,
  kota: false,
  editKota: false,
  area: false,
  editArea: false,
  rekening: false,
  editDataRekening: false,
  lokasi: false,
  editLokasi: false,
  salesman: false,
  editSalesman: false,
  supplier: false,
  editSupplier: false,
  customer: false,
  editCustomer: false,
  kategori: false,
  editKategori: false,
  kodeBarang: false,
  editKodeBarang: false,
  pembayaranSupplier: false,
  namaSupplier: false,
  kadar: false,
  editKadar: false,
  rosok: false,
  editRosok: false,
  parameterTransaksi: false,
  noFaktur: false,
  Kelompok: false,
  totalBon: false,
  error: false,
  sidebar: false,
  sweetProvinsi: "",
  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none"
}


const provinsi = (state = initialState, action) => {
  switch (action.type) {
    case GET_SIDEBAR_UPDATE:
      return {
        ...state,
        sidebar: action.payload.data
      }
    case GET_PROVINSI_LIST:
      return {
        ...state,
        provinsi: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_PROVINSI:
      return {
        ...state,
        editProvinsi: action.payload,
      }
    case GET_KOTA_LIST:
      return {
        ...state,
        kota: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error

      }
    case EDIT_KOTA:
      return {
        ...state,
        editKota: action.payload
      }
    case GET_AREA_LIST:
      return {
        ...state,
        area: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_AREA:
      return {
        ...state,
        editArea: action.payload
      }
    case GET_REKENING_LIST:
      return {
        ...state,
        rekening: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_REKENING_LIST:
      return {
        ...state,
        editDataRekening: action.payload
      }
    case GET_LOKASI_LIST:
      return {
        ...state,
        lokasi: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_LOKASI:
      return {
        ...state,
        editLokasi: action.payload
      }
    case GET_SALESMAN_LIST:
      return {
        ...state,
        salesman: action.payload.data,
        alert: action.payload.alert,
        namaSupplier: action.payload.data.namaSupplier,
        error: action.payload.error
      }
    case EDIT_SALESMAN:
      return {
        ...state,
        editSalesman: action.payload
      }
    case GET_SUPPLIER_LIST:
      return {
        ...state,
        supplier: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_SUPPLIER:
      return {
        ...state,
        editSupplier: action.payload
      }
    case GET_CUSTOMER_LIST:
      return {
        ...state,
        customer: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_CUSTOMER:
      return {
        ...state,
        editCustomer: action.payload
      }
    case GET_KATEGORI_LIST:
      return {
        ...state,
        kategori: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_KATEGORI:
      return {
        ...state,
        editKategori: action.payload
      }
    case GET_PEMBAYARANSUPPLIER_LIST:
      return {
        ...state,
        pembayaranSupplier: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case GET_KADAR_LIST:
      return {
        ...state,
        kadar: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_KADAR:
      return {
        ...state,
        editKadar: action.payload
      }
    case GET_ROSOK_LIST:
      return {
        ...state,
        rosok: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case EDIT_ROSOK:
      return {
        ...state,
        editRosok: action.payload
      }
    case GET_PARAMETER_TRANSAKSI:
      return {
        ...state,
        parameterTransaksi: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case GET_KELOMPOK:
      return{
        ...state,
        Kelompok: action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case GET_KODE_BARANG_LIST:
      return{
        ...state,
        kodeBarang : action.payload.data,
        alert: action.payload.alert,
        error: action.payload.error
      }
    case GET_UUID:
      return{
        ...state,
        noFaktur : action.payload.data
      }
    case TOTAL_BON: 
    return{
      ...state,
      totalBon: action.payload.data
    }
    default:
      return state
  }
}

export default provinsi;