import React, { Component, lazy, Suspense } from 'react';
import './App.css';
import { Route, Switch } from "react-router-dom";
import Skeleton from 'react-loading-skeleton';
const Dashboard = lazy(()=> import ("./Dashboard"))
const Provinsi = lazy(()=> import ("./DataMaster/Provinsi"))
const Kota = lazy(()=> import ("./DataMaster/Kota"))
const Area = lazy(()=> import ("./DataMaster/Area"))
const DaftarRekening = lazy(()=> import ("./DataMaster/DaftarRekening"))
const Lokasi = lazy(()=> import ("./DataMaster/Lokasi"))
const Salesman = lazy(()=> import ("./DataMaster/Salesman"))
const Supplier = lazy(()=> import ("./DataMaster/Supplier"))
const Customer = lazy(()=> import ("./DataMaster/Customer"))
const Koderosok = lazy(()=> import ("./DataMaster/Koderosok"))
const SaldoAwal = lazy(()=> import ("./DataMaster/SaldoAwal"))
const Kadar = lazy(()=> import ("./DataMaster/Kadar"))
const KlasifikasiBarang = lazy(()=> import ("./DataMaster/KlasifikasiBarang"))
const ParameterTransaksi = lazy(()=> import ("./DataMaster/ParameterTransaksi"))
const PindahBarang = lazy(()=> import ('./Stocking/PindahBarang'))
const MutasiBarang = lazy(()=> import ('./Stocking/MutasiBarang'))
const MutasiCiok = lazy(()=> import ('./Stocking/MutasiCiok'))
const KirimMasak = lazy(()=> import ('./Stocking/KirimMasak'))
const TerimaMasak = lazy(()=> import ('./Stocking/TerimaMasak'))
const KirimBahan = lazy(()=> import ('./Stocking/KirimBahan'))
const TerimaSupplier = lazy(()=> import ('./Transaksi/TerimaSupplier'))
const ReturnSupplier = lazy(()=> import ('./Transaksi/ReturnSupplier'))
const TolakanReturnSupplier = lazy(()=> import ('./Transaksi/TolakanReturnSupplier'))
const PengambilanBarcod = lazy(()=> import ('./Transaksi/PengambilanBarcode'))
const PembayaranSupplier = lazy(()=> import ('./FICO/PembayaranSupplier'))
const Kategori = lazy(()=> import ('./DataMaster/Kategori'))
const KodeBarang = lazy(()=> import ('./DataMaster/KodeBarang'))
const Kelompok = lazy(()=> import ('./DataMaster/Kelompok'))
const ClosePengambilanBarcode = lazy(()=> import ('./Transaksi/ClosePengambilanBarcode'))
const TransaksiTimbangBarang = lazy(()=> import ('./Transaksi/TransaksiTimbangBarang'))
const BatalPengambilanTimbang = lazy(()=> import ('./Transaksi/BatalPengambilanTimbang'))
const TransaksiPenjualan = lazy(()=> import ('./Transaksi/TransaksiPenjualan'))
const ReturnPenjualan = lazy(()=> import ('./Transaksi/ReturnPenjualan'))
const RekapBonCustomer = lazy(()=> import ('./Transaksi/RekapBonCustomer'))
const KelolaUangBank = lazy(()=> import ('./FICO/KelolaUangBank'))
const KelolaUangKas = lazy(()=> import ('./FICO/KelolaUangKas'))
const MutasiCashDanBank = lazy(()=> import ('./FICO/MutasiCashDanBank'))
const KlasifikasiSupplier = lazy(()=> import ('./FICO/KlasifikasiSupplier'))
const PembayaranHutangSupplier = lazy(()=> import ('./FICO/PembayaranHutangSupplier'))
const TolakanHutangSupplier = lazy(()=> import ('./FICO/TolakaHutangSupplier'))
const TitipanSupplier = lazy(()=> import ('./FICO/TitipanSupplier'))
const KlasifikasiCustomer = lazy(()=> import ('./FICO/KlasifikasiCustomer'))
const TitipanGrCustomer = lazy(()=> import ('./FICO/TitipGrCustomer'))
const PembayaranPerBon = lazy(()=> import ('./FICO/PembayaranPerBon'))
const TransferBalance = lazy(()=> import ('./FICO/TransferBalance'))
const TolakanPembayaranCustomer = lazy(()=> import ('./FICO/TolakanPembayaranCustomer'))
const ValidasiTransferCustomer = lazy(()=> import ('./FICO/ValidasiTransferCustomer'))
const ValidasiPembayaranGiro = lazy(()=> import ('./FICO/ValidasiPembayaranGiro'))
const ViewPDF = lazy(()=> import ('./Transaksi/ViewPDF'))

export const RouteLink = () => {
    return (
        <Suspense fallback={<div><Skeleton width={400} height={400}/></div>}>
        <Switch>
              <Route exact path="/ayugold.com/" component={Dashboard} />
              <Route exact path="/ayugold.com/ViewPDF" component={ViewPDF} />
              <Route exact path="/ayugold.com/dashboard" component={Dashboard} />
              <Route path="/ayugold.com/provinsi" component={Provinsi} />
              <Route path="/ayugold.com/kota" component={Kota} />
              <Route path="/ayugold.com/area" component={Area} />
              <Route path="/ayugold.com/rekening" component={DaftarRekening} />
              <Route path="/ayugold.com/lokasi" component={Lokasi} />
              <Route path="/ayugold.com/salesman" component={Salesman} />
              <Route path="/ayugold.com/supplier" component={Supplier} />
              <Route path="/ayugold.com/customer" component={Customer} />
              <Route path="/ayugold.com/koderosok" component={Koderosok} />
              <Route path="/ayugold.com/saldoawal" component={SaldoAwal} />
              <Route path="/ayugold.com/kadar" component={Kadar} />
              <Route path="/ayugold.com/KlasifikasiBarang" exact component={KlasifikasiBarang} />
              <Route path="/ayugold.com/kodebarang" exact component={KodeBarang} />
              <Route path="/ayugold.com/parameter" component={ParameterTransaksi} />
              <Route path="/ayugold.com/pindahbarang" component={PindahBarang} />
              <Route path="/ayugold.com/mutasibarang" component={MutasiBarang} />
              <Route path="/ayugold.com/mutasiciok" component={MutasiCiok} />
              <Route path="/ayugold.com/kirimmasak" component={KirimMasak} />
              <Route path="/ayugold.com/terimamasak" component={TerimaMasak} />
              <Route path="/ayugold.com/kirimbahan" component={KirimBahan} />
              <Route path="/ayugold.com/terimasupplier" component={TerimaSupplier} />
              <Route path="/ayugold.com/returnsupplier" component={ReturnSupplier} />
              <Route path="/ayugold.com/tolakanreturnsupplier" component={TolakanReturnSupplier} />
              <Route path="/ayugold.com/ambilbarcode" component={PengambilanBarcod} />
              <Route path="/ayugold.com/bayarsupplier" component={PembayaranSupplier} />
              <Route path="/ayugold.com/kategori" exact component={Kategori} />
              <Route path="/ayugold.com/kelompok" exact component={Kelompok} />
              <Route path="/ayugold.com/closePengambilanBarang" exact component={ClosePengambilanBarcode} />
              <Route path="/ayugold.com/transaksiTimbangBarang" exact component={TransaksiTimbangBarang} />
              <Route path="/ayugold.com/batalPengambilanTimbangan" exact component={BatalPengambilanTimbang} />
              <Route path="/ayugold.com/TransaksiPenjualan" exact component={TransaksiPenjualan} />
              <Route path="/ayugold.com/TransaksiPenjualan" exact component={ReturnPenjualan} />
              <Route path="/ayugold.com/RekapBonPenjualan" exact component={RekapBonCustomer} />
              <Route path="/ayugold.com/KelolaUangBank" exact component={KelolaUangBank} />
              <Route path="/ayugold.com/KelolaUangKas" exact component={KelolaUangKas} />
              <Route path="/ayugold.com/MutasiCashDanBank" exact component={MutasiCashDanBank} />
              <Route path="/ayugold.com/KlasifikasiSupplier" exact component={KlasifikasiSupplier} />
              <Route path="/ayugold.com/PembayaranHutangSupplier" exact component={PembayaranHutangSupplier} />
              <Route path="/ayugold.com/TolakanHutangSupplier" exact component={TolakanHutangSupplier} />
              <Route path="/ayugold.com/TitipanSupplier" exact component={TitipanSupplier} />
              <Route path="/ayugold.com/KlasifikasiCustomer" exact component={KlasifikasiCustomer} />
              <Route path="/ayugold.com/TitipanGrCustomer" exact component={TitipanGrCustomer} />
              <Route path="/ayugold.com/PembayaranPerBon" exact component={PembayaranPerBon} />
              <Route path="/ayugold.com/TransferBalance" exact component={TransferBalance} />
              <Route path="/ayugold.com/TolakanPembayaranCustomer" exact component={TolakanPembayaranCustomer} />
              <Route path="/ayugold.com/ValidasiTransferCustomer" exact component={ValidasiTransferCustomer} />
              <Route path="/ayugold.com/ValidasiPembayaranGiro" exact component={ValidasiPembayaranGiro} />
            </Switch>
            </Suspense>
    )
}